.pagination-container {
    list-style-type: none;
    margin: 10px;
    padding: 0;
    overflow: hidden;
    background-color: hsl(0, 5%, 76%);
    cursor: pointer;
    display: grid;
}

.list-items {
    display: block;
    color: rgb(17, 17, 17);
    text-align: center;
    padding: 16px;
    text-decoration: none;
    float: left;
    box-sizing: border-box;
    padding-top: 5px;
}

.list-items:hover {
    background-color: hsla(0, 2%, 54%, 0.836);
}

.ulPag {
    margin-top: 0px;
}